var hh = document.getElementById("hh");
var mm = document.getElementById("mm");
var ss = document.getElementById("ss");
var YYYY = document.getElementById("YYYY");
var MM = document.getElementById("MM");
var DD = document.getElementById("DD");
var WWW = document.getElementById("WWW");
function toStringWithZeroPadding(n, stringLength) {
    var nStr = n.toString(10);
    if (nStr.length >= stringLength)
        return nStr;
    return ("0".repeat(stringLength) + nStr).slice(-stringLength);
}
function toDayString(dayOfTheWeek) {
    var daysInWeek = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
    ];
    return daysInWeek[dayOfTheWeek];
}
function updateDigitalClockText(date) {
    if (hh)
        hh.innerHTML = toStringWithZeroPadding(date.getHours(), 2);
    if (mm)
        mm.innerHTML = toStringWithZeroPadding(date.getMinutes(), 2);
    if (ss)
        ss.innerHTML = toStringWithZeroPadding(date.getSeconds(), 2);
    if (YYYY)
        YYYY.innerHTML = toStringWithZeroPadding(date.getFullYear(), 4);
    if (MM)
        MM.innerHTML = toStringWithZeroPadding(date.getMonth() + 1, 2);
    if (DD)
        DD.innerHTML = toStringWithZeroPadding(date.getDate(), 2);
    if (WWW)
        WWW.innerHTML = "" + toDayString(date.getDay());
}
export { updateDigitalClockText };
