var hhmmss = document.querySelector("#hhmmss");
var YYYYMMDD = document.querySelector("#YYYYMMDD");
var WWW = document.querySelector("#WWW");
function alignTextsSpaceBetween(padding) {
    if (!hhmmss || !YYYYMMDD || !WWW)
        return;
    var hhmmssBBox = hhmmss.getBBox();
    var YYYYMMDDBBox = YYYYMMDD.getBBox();
    YYYYMMDD.setAttribute("y", (hhmmssBBox.y + hhmmssBBox.height + YYYYMMDDBBox.height + padding).toString(10));
    YYYYMMDDBBox = YYYYMMDD.getBBox();
    var WWWBBox = WWW.getBBox();
    WWW.setAttribute("y", (YYYYMMDDBBox.y + YYYYMMDDBBox.height + WWWBBox.height + padding).toString(10));
}
function moveDigitalClock(dy) {
    if (!hhmmss || !YYYYMMDD || !WWW)
        return;
    hhmmss.setAttribute("y", (parseInt(hhmmss.getAttribute("y") || "0", 10) + dy).toString(10));
    YYYYMMDD.setAttribute("y", (parseInt(YYYYMMDD.getAttribute("y") || "0", 10) + dy).toString(10));
    WWW.setAttribute("y", (parseInt(WWW.getAttribute("y") || "0", 10) + dy).toString(10));
}
function layOutDigitalClock() {
    alignTextsSpaceBetween(10);
}
export { layOutDigitalClock, moveDigitalClock };
